const quotesSlider = ($, isWithImage) => {
	const owl = $('.quotes-slider');
	owl.owlCarousel({
		dots: false,
		items: 1
	});

	const leftBtn = $('.quotes-slider__prev');
	const rightBtn = $('.quotes-slider__next');


	let changed = null;

	owl.on('changed.owl.carousel', () => {
		changed = true;
	});

	leftBtn.click(() => {
		owl.trigger('prev.owl.carousel');
		leftBtn.blur();
		if (changed) {
			changed = false;
			console.log('left');
		}
	});


	rightBtn.click(() => {
		owl.trigger('next.owl.carousel');
		rightBtn.blur();
		if (changed) {
			changed = false;
			console.log('right');
		}
	});
};

module.exports = quotesSlider;



